<template>
  <div class="header">
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="onClickLeft"></van-nav-bar>
    <div class="bg">
      <h4>Q1:</h4>
      <p class="bg-user"></p>
    </div>
    <div class="bg">
      <h4>Q2: </h4>
      <p class="bg-user">
        <br/>
