      <p class="bg-user">
        <br>
        <br>150
        <br>50
        <br></p>
    </div>
    <div class="bg">
      <h4>Q4:</h4>
      <p class="bg-user">
        <br>
        <br></p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "detail",
    methods: {
      onClickLeft() {
        this.$router.go(-1)
      }
    }
  }
</script>

<style scoped>
  .header {
    background: white;
  }

  .van-nav-bar {
    background: #3190e8;
  }

  .van-nav-bar__title {
    color: white;
    font-size: .18rem;
    font-weight: bold;
  }

  .van-icon {
    color: white;
    font-size: .25rem;
    margin-left: -.09rem;
    font-weight: 100;
  }

  .bg-user {
    font-size: .14rem;
    line-height: .2rem;
    margin: .1rem 0 0 .11rem;
  }

  h4 {
    padding-top: .14rem;
    font-size: .16rem;
    font-weight: 100;
    margin-left: .11rem;
    color: #333;
  }
</style>



